.update-modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.05);
    text-align: center;
}

.update-modal-content {
    background-color: #fff;
    margin: 10% auto;
    padding: 20px;
    border: 1px solid #ddd;
    width: 60%;
    max-width: 600px;
    max-height: 500px;
    position: relative;
    border-radius: 8px;
    overflow: auto;
}

.update-modal-content input[type="text"],
.update-modal-content input[type="date"],
.update-modal-content select {
    width: 100%;
}

.table-container-update-user {
    overflow: auto;
}

.close {
    color: #aaa;
    position: absolute;
    top: 10px;
    right: 10px;
    font-size: 24px;
    cursor: pointer;
}

.close:hover {
    color: #333;
}

.product-details {
    width: 100%;
    border-collapse: collapse;
    margin-top: 10px;
}

.product-details td {
    padding: 12px;
    border-bottom: 1px solid #ddd;
}

.product-details td:first-child {
    font-weight: bold;
    width: 30%;
}

.product-details td:last-child {
    width: 70%;
}